header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}
.header__container {
    text-align: center;
    height: 100%;
    position:relative;
}

/*======= CTA ======*/
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
    align-items: center;
}

/*=========== HEADER SOCIAL==========*/
.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 10rem;
    bottom: 3rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2 rem;
    background: var(--color-primary);
}
/*=========ME===========*/
.me{
    background: linear-gradient(var(--color-primart), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/* ============ SCROLL DOWN =========== */
.scroll__down{
    position: absolute;
    right: 5rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;    
}

/* ================ MEDIA QUERIES (Medium) ============*/
@media screen and (max-width: 1024px) {
    header{
        height: 68vh;
    }
}

/* ================ MEDIA QUERIES (Small) ============*/
@media screen and (max-width: 600px) {
    header{
        height: 100vh;
    }
    .header__socials,
    .scroll__down {
        display: none;
    }
}